<template>
  <div class="success">
        <div class="isChange padd">
            <p class="successs iconfont icon-chenggong"></p>
            <h2 class="one_t tc">下单成功</h2>
            <p class="two_t_m">已收到您的订单，我们会尽快安排配送敬请您耐心等候</p>
            <button class="bluebtn geren" @click="$router.push('/personalCenter/orderdetail')">查看订单详情</button>
            <button class="whitebtn" @click="$router.push('/home/today')">返回商城首页</button>
        </div>
  </div>
</template>

<script>
export default {
  name: "Success",
  data() {
    return {};
  },
  methods: {},
  components: {}
};
</script>

<style scoped lang="less">
.success {
  font-size: 0.16rem;
  .isChange {
    .successs {
      width: 0.93rem;
      height: 0.93rem;
      font-size: 0.93rem;
      margin: 0 auto;
      margin-top: 0.3rem;
      margin-bottom: 0.33rem;
    }
    .geren {
      margin: 0.3rem 0rem 0.15rem 0rem;
    }
    p {
        width: 2.72rem;
        margin: 0 auto;
        text-align: center;
    }
  }
}
</style>



